.container{
	width: 100%;
	height: auto;
	padding: 20px;
	margin-left: 40px;
}

.items{
	width: 700px;
	height: 130px;
	margin-top: 30px;
	display: flex;
	justify-content: space-between;
	padding: 20px;
	border: 1px solid #cdcdcd;
	border-radius: 10px;
}
.photo{
	width: 100px;
	height: 100px;
	border-radius: 50%;
}
.profile{
	width:25%;
}
button{
	width: 100px;
	height: 30px;
	margin-left:1px;
	margin-top: 6px;
	background-color: #9bbb;
	border: 1px solid #9bbb;
}
.inform{
	width: 45%;
}
.inform h2{
	font-size: 20px;
}
.inform p{
	font-size: 16px;
	font-style: italic;
	padding-top: 15px;
}
.location{
	width:30% ;
	margin-left: 50px;
}
.location h2{
	font-size: 18px;
	margin-left: 100px;
}
.location h3{
	font-size: 16px;
	margin-top: 70px;
	margin-left: 100px;
}

.selectedPage{
	font-weight: 700;
	color:red;
	cursor:pointer;
}
.page{
margin: 0 auto;
display: flex;
justify-content: space-between;
width: 300px;
margin-top: 20px;
color: #808080;
}

.preloader{
	margin-left: 360px;
	width: 56px;
	height: 56px;
}